import React from "react"

import List from "./List"

//React-Component - ÑÑ‚Ð¾ Ð¾ÑÐ½Ð¾Ð²Ð½Ð¾Ð¹ Ð¾Ð±ÑŠÐµÐºÑ‚ ÐŸÑ€Ð¸Ð»Ð¾Ð¶ÐµÐ½Ð¸Ñ
class App extends React.Component{
  //Ð¤ÑƒÐ½ÐºÑ†Ð¸Ñ ÐºÐ¾Ñ‚Ð¾Ñ€Ð°Ñ Ð½Ð°ÑÑ‚Ñ€Ð°Ð¸Ð²Ð°ÐµÑ‚ REact-Component
  constructor(props){
    //ÐŸÐµÑ€ÐµÐ´Ð°ÐµÐ¼ Ð¾ÑÐ½Ð¾Ð²ÑƒÐ½ÑŽ Ð½Ð°ÑÑ‚Ñ€Ð½Ð¾Ð¹ÐºÑƒ Ð² Ð±Ð¸Ð±Ð»Ð¸Ð¾Ñ‚ÐµÐºÑƒ React 
    super(props)

    //Ð¡Ð¾Ð·Ð´Ð°ÐµÐ¼ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€Ñ‹, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ð´Ð¾Ð»Ð¶Ð½Ñ‹ Ñ…Ñ€Ð°Ð½Ð¸Ñ‚ÑÑ Ð² Ð¿Ð°Ð¼ÑÑ‚Ð¸  !Ð¿Ñ€Ð¸Ð»Ð¾Ð¶ÐµÐ½Ð¸Ñ!
    this.state = {
      items: localStorage.getItem("items") !=null ? JSON.parse(localStorage.getItem("items")) : [{"text": "Ð’Ñ‹Ð±ÐµÑ€Ð¸ Ð´ÐµÐ»Ð¾! (Ð¼Ð¾Ð¶ÐµÑˆÑŒ ÑƒÐ´Ð°Ð»Ð¸Ñ‚ÑŒ)"}]
    }
    //ÐŸÐ¾Ð´ÐºÐ»ÑŽÑ‡Ð°ÐµÐ¼ Ðº Event-Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ Ñ„ÑƒÐ½ÐºÑ†Ð¸ÑŽ ÐšÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ð°
    this.addTodo = this.addTodo.bind(this)
    this.deleteTodo = this.deleteTodo.bind(this)
  }
  //Ð¸Ð²ÐµÐ½Ñ‚ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ñ Ð²Ð¾ Ð²Ñ€ÐµÐ¼Ñ ÑÐ¾Ð±Ñ‹Ñ‚Ð¸Ñ submit Ð´Ð»Ñ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ Ð½Ð¾Ð²Ð¾Ð³Ð¾ Ð´ÐµÐ»Ð°Ñ‚ÑŒ
  addTodo(e){
    //ÐžÑ‚Ð¼ÐµÐ½ÑÐµÐ¼ Ð¿ÐµÑ€ÐµÐ·Ð°Ð³Ñ€ÑƒÐ·ÐºÑƒ ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ñ‹
    e.preventDefault()
    //Ð¸Ð·Ð¼ÐµÐ½Ð¸Ñ‚ÑŒ state
    let form = e.target
    let newTodo = form.todoInput.value
    //Ð¸Ð·Ð¼ÐµÐ½Ð¸Ñ‚ÑŒ state
    // .setState((state)=>{.........     return {state}})
    //Ð¾Ð±Ð½Ð¾Ð²Ð¸Ñ‚ÑŒ state Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð¸Ñ‚ÑŒ Ð² items Ð½Ð¾Ð²Ð¾Ðµ Ð´ÐµÐ»Ð¾
    this.setState( (state)=>{
      //Ð±ÐµÑ€ÐµÐ¼ Ð¼Ð¾Ð·Ð³
      let newState = state
      //Ð¾Ð±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð¿Ð°Ð¼ÑÑ‚ÑŒ items
      if (newTodo !== "") {
        newState.items.push({text: newTodo})
      }
      else {alert("Ð½ÐµÐ»ÑŒÐ·Ñ Ñ‚Ð°Ðº")}
      localStorage.setItem("items", JSON.stringify(newState.items))
      return {newState}
  })
  //Ð¾Ñ‡Ð¸Ñ‰Ð°Ñ‚ÑŒ input
  form.todoInput.value = ""
  }

  
  deleteTodo(e){
    //Ð’Ñ‹Ñ…Ð¾Ð´Ð¸Ð¼ Ðº Ñ€Ð¾Ð´Ð¸Ñ‚ÐµÐ»ÑŽ, Ð±ÐµÑ€ÐµÐ¼ Ð¿ÐµÑ€Ð²Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ (ÑÑ‚Ð¾ Ð½Ð°Ñˆ span)
    let todoIndex = e.target.parentNode.childNodes[0].innerHTML
    //Ð¾Ð±Ð½Ð¾Ð²Ð¸Ñ‚ÑŒ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ state 
    //*********
    this.setState((state)=>{
      let newState = state
      let items = newState.items
      items.splice(todoIndex-1,1)
      newState.items = items;
      localStorage.setItem("items", JSON.stringify(newState.items))
      return {newState}
    })
  }

//Ñ€ÐµÐ½Ð´ÐµÑ€ Ð½Ð°ÑˆÐµÐ³Ð¾ ÐºÐ¾Ð¼Ð¿Ð¾Ð½ÐµÐ½Ñ‚Ð°
  render(){
    let elem=
    <div id="todo-list" className="container-md">
      <div className="row justify-content-center">
        <div className='col-4'>
          <form className="row" onSubmit={this.addTodo}>
            <input name="todoInput" type="text" className="col form-control" placeholder="Ð’Ð²ÐµÐ´Ð¸Ñ‚Ðµ Ð´ÐµÐ»Ð¾"/>
            <button type='submit' className="col-3 btn btn-danger">button</button>
          </form>
        </div>
      </div>
      <div className="row justify-content-center">
        <div className='col-4'>
          <List itemList={this.state.items} deleteTodo={this.deleteTodo}/>
        </div>
      </div>
    </div>
    return elem
  }
}
export default App;
