function List(props) { 

    //<div>N) text ðŸ˜Š</div>
    let items = props.itemList.map((item, index)=>{
        //1. ÐšÑƒÐ¿Ð¸Ñ‚ÑŒ Ð¼Ð¾Ð»Ð¾ÐºÐ¾ (Ð¸ÐºÐ¾Ð½ÐºÐ°)

        return <div className='row'>
        <span className='col-1'>{index+1}.</span> 
        <div className="col-9">{item.text}</div> 
        <i onClick={props.deleteTodo} className="bi bi-trash-fill col-1"></i></div>
    })
    let elem =
    <div >
        {items}
    </div>
    return elem
    }
    
    export default List;